import React from "react";
import { Link } from "react-router-dom";
import "./App.css";

const NavBar = () => (
  <nav>
    <ul>
      <li>
        <Link to="/">Home</Link>
      </li>
      <li>
        <Link to="/about">About Page</Link>
      </li>
      <li>
        <Link to="/articles-list">Articles List</Link>
      </li>
    </ul>
  </nav>
);

export default NavBar;
